.feedbackBlock {
  display: block;
  box-sizing: border-box;
  max-width: 350px;
  height: auto;

  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  padding: 15px;

  background-color: #fff;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
  width: 500 px;
  margin: auto;
  text-align: center;
  border: 5 px dashed rgba(5, 30, 255, 0.145);
  border-radius: 10 px;
  padding: 30 px;
}

.section {
  text-align: center;
}

.statistic,
.statistic_title {
  margin-left: 20px;
}

.button {
  display: inline-block;

  padding: 10px 20px;
  margin: 10px;

  background-color: #00abdf;

  border: none;
  color: white;

  font-size: 12px;
  font-weight: 700px;
  text-align: center;
  text-decoration: none;

  cursor: pointer;
}

.button:hover {
  background-color: rgb(219, 0, 0);
}

.voiting_percent {
  padding: 15px;
  background-color: #00abdf;

  color: azure;
}

.options {
  display: flex;

  justify-content: space-between;

  margin: -10px;
}
